nav {
  margin-bottom: 20px; }

.poster-boxs {
  margin-bottom: 20px;
  overflow: hidden;
  cursor: pointer; }

select {
  display: block; }

.m-0 {
  margin: 0px; }

.poster-details {
  padding: 10px 15px;
  color: #fff; }
  .poster-details h5 {
    margin-top: 0px; }
  .poster-details p {
    font-size: 12px;
    text-align: justify; }
  .poster-details footer {
    margin-top: 25px; }
    .poster-details footer > div {
      display: flex;
      align-items: center; }
      .poster-details footer > div.right-align {
        justify-content: flex-end; }

.icon {
  width: 16px;
  height: 16px; }

.col.order-select {
  border-bottom: 1px solid #ccc;
  padding-bottom: 20px;
  display: flex;
  justify-content: flex-end; }
  .col.order-select > div {
    width: auto;
    display: flex; }

.quicklink {
  position: fixed;
  right: 0px;
  z-index: 999; }
  .quicklink > div {
    width: 40px;
    height: 40px;
    background-color: slateblue;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #fff;
    border-radius: 5px; }
    .quicklink > div a, .quicklink > div button {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: slateblue;
      color: #fff;
      box-shadow: none;
      border-radius: 5px; }

.poster-overlay {
  background: #000;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  width: 100%;
  bottom: -1000px;
  transition: 0.8s ease; }

.selected .poster-overlay {
  bottom: 0; }
